<template>
    <div class="wrap-div" ref="wrapHeight">
        <el-tabs @tab-click="getRefresh">
            <el-tab-pane label="配送异常">
                <div class="height-div">
                    <my_point ref="my_point"></my_point>
                </div>
            </el-tab-pane>
            <el-tab-pane label="生产异常">
                <div class="heigth-div scrollBar">
                    <div class="point-div">
                        <span class="spanI">172号车超过10分钟未进磅</span>
                        <span class="spanII">2018-3-27 14:30</span>
                    </div>
                    <div class="point-div">
                        <span class="spanI">001号线生产超时一小时</span>
                        <span class="spanII">2018-3-27 14:30</span>
                    </div>
                </div>
                <!-- <diaoduzhang_scpsyc ref="diaoduzhang_scpsyc" :wrapHeight="wrapHeight"></diaoduzhang_scpsyc> -->
            </el-tab-pane>
            <el-tab-pane label="开盘异常">
                <el-table
                    :data="abnormalOrderData"
                    :height="tabHeight"
                    border
                    highlight-current-row
                    class="th-center tab-pad">
                    <el-table-column prop="buildpartname" label="部位" align="center" min-width="2"></el-table-column>
                    <el-table-column prop="builddate" label="计划开盘日期" align="center" min-width="3"></el-table-column>
                    <el-table-column prop="buildsite" label="工程名称" align="center" min-width="5"></el-table-column>
                    <el-table-column prop="cancelname" label="类型" align="center" min-width="3"></el-table-column>
                </el-table>
                <!-- <diaoduzhang_kpyc :wrapHeight="wrapHeight" ref="diaoduzhang_kpyc"></diaoduzhang_kpyc> -->
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
    export default {
        components: {
            'my_point':resolve => require(['./my_point'], resolve),
            'diaoduzhang_scpsyc':resolve => require(['./diaoduzhang_scpsyc'], resolve),
            'diaoduzhang_kpyc':resolve => require(['./diaoduzhang_kpyc'], resolve),
        },
        data(){
            return{
                wrapHeight: 270,
                tabHeight:270,
                apiUrl:'/finance/erpMes/erp-prod-send',
                abnormalSendData:[],
                abnormalOrderData:[],
                someDataI:[],
                someData:[],
                searchForm:{
                    exts:{
                       builddate_ge_date:this.recentDate(0),
                       builddate_le_date:this.recentDate(1),
                    },
                    status:-2
                },
                content:'senddate_ge_date='+this.recentDate(0)+'&senddate_le_date='+this.recentDate(1)+'&sendstatus=-1',
            }
        },
        activated(){
            this.init();
        },
        mounted() {
            this.init();
            setTimeout(()=>{
                this.getDivHeight();
            },20)
        },
        methods: {
            init(){
                this.getlist();
                this.getSomeData();
                this.getSomeDataI();
            },
            getDivHeight() {
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight;
            },
            getlist(){
                let tempData = [];
                let tempDataI = [];
                this.someData = [];
                this.someDataI = [];
                this.abnormalOrderData = [];
                this.fetch('/mktmfc/clientOrder/findAll?pageNo=1' + '&pageSize=1000' + '&orderBys=createtime_desc',this.searchForm)
                    .then(result => {
                        if(result.status === 200){
                            tempData = result.data.list;
                            for(let i=0; i<tempData.length; i++){
                                tempData[i].builddate = tempData[i].builddate.substring(5,16)
                                //tempData[i].cancelname = _.filter(this.someData,{itemkey:tempData[i].canceltype})[0].itemname
                                for(let j=0; j<this.someData.length; j++){
                                    if(tempData[i].canceltype === this.someData[j].itemvalue){
                                        tempData[i].cancelname = this.someData[j].itemname
                                    }
                                }
                            }
                            this.abnormalOrderData = tempData
                        }else {
                            this.$message({
                                message:this.returnIntercept(result),
                                type:'error'
                            });
                            
                        }
                    })
                this.getAjax(this.apiUrl+'?'+'pageNo=1'+'&pageSize=1000'+'&'+this.content)
                    .then(result => {
                        if (result.status === 200) {
                            if(result.data.list[0] != null){
                                tempDataI = _.initial(result.data.list);
                                for(let i=0; i<tempDataI.length; i++){
                                    if(tempDataI[i].canceltype === null){
                                        tempDataI[i].cancelname = '其他原因'
                                    }else {
                                        for(let j=0; j<this.someDataI.length; j++){
                                            if(tempDataI[i].canceltype === this.someDataI[j].itemvalue){
                                                tempDataI[i].cancelname = this.someDataI[j].itemname
                                            }
                                        }
                                    }
                                }
                                this.abnormalSendData =  tempDataI;
                            }
                        }else{
                            this.$message({
                                message:this.returnIntercept(result),
                                type: 'error'
                            });
                        }
                    })
            },

            //获取数据字典
            getSomeData() {
                this.postDicData([{'nodekey':'Sell_CancelType'}]).then( (result)=>{
                    if (result.status === 200) {
                        this.someData = result.data['Sell_CancelType'];      
                    }
                })
            },

            //获取数据字典
            getSomeDataI() {
                this.postDicData([{'nodekey':'ErpProd_SendCancelType'}]).then( (result)=>{
                    if (result.status === 200) {
                        this.someDataI = result.data['ErpProd_SendCancelType'];      
                    }
                })
            },

            //日期显示格式化
            format(row,column,cellValue) {
                return this.formatDate(cellValue,'ymdhM');
            },

            getRefresh(){
                this.init();
            }
        }
    }
</script>
<style scoped lang="less" type="text/less">
    .wrap-div{
        height: 100%;
    }
    .point-div{
        margin-top: 10px;
        margin-left: 10px;
        display: flex;
        justify-content: space-between;
        .spanII{
            margin-right: 10px;
        }
    }
    .heigth-div{
        height: 95%;
        overflow-y:auto
    }
</style>


